import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { Star, Slack, Download } from "react-feather";

const EditorialItem = ({ data }) => (
  <article className="service-container">
    <li className="editorial">
      <span className="title">{data.title}</span>
      <ul className="data">
        {data.data.map((d) => (
          <li key={d}>
            <div dangerouslySetInnerHTML={{ __html: d }} />
          </li>
        ))}
      </ul>
    </li>
  </article>
);

EditorialItem.propTypes = {
  data: PropTypes.shape({
    title: PropTypes.string.isRequired,
    data: PropTypes.array.isRequired,
  }).isRequired,
};

export default EditorialItem;
