import React from "react";
import PropTypes from "prop-types";

import EditorialItem from "./Editorial/editorialItem";

import { FileText } from "react-feather";
import { Slack } from "react-feather";

const Editorial = ({ data }) => (
  <div className="editorial">
    <div className="link-to" id="editorial" />
    <div className="title">
      <h2>
        <span className="mr-2">
          <FileText color="red" size={16} />
        </span>
        <span> Editorial and Other Services</span>
      </h2>
    </div>

    <ul>
      {data.map((editorial) => (
        <EditorialItem data={editorial} key={editorial.title} />
      ))}
    </ul>
  </div>
);

Editorial.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      data: PropTypes.array,
    })
  ),
};

Editorial.defaultProps = {
  data: [],
};

export default Editorial;
